<template> <!--SFC-->
    <div id="algo">
        <h1>Two way data binding</h1>
        <input type="text" v-model="nombre">
        <h2 id="contenido">{{ nombre }}</h2>
        <button @click="cambiarNombre">Cambiar modelo</button>
    </div>
</template>

<script>
export default {
    name: 'nuevoComponente',
    data: function() {
        return {
            nombre: 'Elena'
        }
    },
    methods: {
        cambiarNombre: function() {
            this.nombre = 'Cambiamos el modelo de datos'
        }
    }
}
</script>

<style scoped>
#algo {
    background-color: slateblue;
}
button {
    padding: 5px;
    margin-bottom: 10px;
    border-radius: 5px;
    border: 0px;
    color: rgb(18, 155, 173);
    font-size: 18px;
}

</style>