<template>
  <div id="app">
    <img id="logo" alt="Vue logo" src="./assets/logo.png">
    <hr>
    <nuevoComponente></nuevoComponente>
    <!--one way data binding es enviar info desde el modelo de datos en el script hacia el template-->
    <!--La notacion doble llave es para hacer one way data binding en el CONTENIDO de los elementos-->
    <p id="parrafo1">{{ info }}</p>
    <!--v-bind es para hacer one way data binding en los ATRIBUTOS de los elementos-->
    <input id="cajaTexto1" type="text" v-bind:value="mensaje">
    <div class="imagenes">
      <img v-bind:src="rutaImagen" alt="nombreImagen">
      <img :src="urlImagen" alt="nombreImagen2">
    </div>
    <!--Puedo usar componentes creados como etiquetas html-->
  </div>
</template>

<script>
import nuevoComponente from '@/components/nuevoComponente.vue';

export default {
  name: 'App',
  components: {
    //registramos el componente importado en esta secci√≥n
    nuevoComponente
  },
  data: function() {
    return {
      info: 'One way data binding',
      mensaje: 'Usando directiva v-bind',
      //cualquier img traida desde internet con url se puede alojar en propiedad del modelo de datos, sin arroba y sin require
      //si son imgs locales hay que anteponer la funcion "require" para que obtenga la img y la proporcione al proyecto. ruta de img no funciona como cadena de texto
      rutaImagen: require('@/assets/img/space.webp'),
      nombreImagen: 'espacio',
      urlImagen: 'https://www.latercera.com/resizer/G0ESF-9FgSBDf2eXW5fR-3iM2bM=/900x600/smart/arc-anglerfish-arc2-prod-copesa.s3.amazonaws.com/public/XFAPQHDLVVHLFPRLE7U6V7FX4A.jpg',
      nombreImagen2: 'espacio2'
    }
  }
}
</script>

