  height: 30px;
  font-size: 22px;
  margin-bottom: 10px;
}
.imagenes {
  display: flex;
  justify-content: space-evenly;
}
img {
  width: 30%;
  height: 20%;
}
#logo {
  width: 100px;
}
</style>
